<template>
   <div class="coin">
       <Head :data='data[1]+"地址"'></Head>
       <div class='coin_cen'>
           <div class='coin_title'>
               <div>
                   <span style='margin-right: 20px;'>
                       <img src="../../../../static/images/background/bi.png" alt="">
                   </span>
                   <span>
                       {{data[0]}} {{data[1]}}
                   </span>
               </div>
               <div>
                   <span>
                       地址：
                       <i class='site'>{{Site}}</i>
                   </span>
               </div>
               <div class='code'>
                   <div id='qrcode' ref='qrcode'></div>
               </div>
                <div class='btn'>
                    <van-button
                    class='btn1' 
                    :data-clipboard-text="Site" 
                    @click="copy"
                    size="large">复制地址</van-button>
                    <!-- <van-button class='btn2' size="large">保存到相册</van-button> -->
                </div>
           </div>
       </div>
   </div>
</template>

<script>

import Head from '../../Head';
// 复制地址插件
import Clipboard from 'clipboard';
// 地址转换二维码插件
import QRCode from 'qrcodejs2';

export default {
    name: 'coin',
    components: { Head },
    data() {
        return {
            data:this.$route.query.data,
            Site:'',
        }
    },
    created(){
        this.site();
    },
    methods:{
        copy(){
            var clipboard = new Clipboard('.btn1');
            clipboard.on('success', e => {
                this.$dialog.alert({
                    message: '复制成功'
                })
                // 释放内存
                clipboard.destroy()
            })
        },
        qrcode(address){
            let qrcode = new QRCode('qrcode',{
                width: 150, // 设置宽度，单位像素
                height: 150, // 设置高度，单位像素
                text: address // 设置二维码内容或跳转地址
            })
        },
        site(){
            let url = '/blockchain/getTokenAddress';
            this.$http.post(url,{
                cmd:'123',
                body:{
                    uid:localStorage.uid,
                    appid:localStorage.appid,
                    token:this.$route.query.data[1]
                }
            }).then( (res) => {
                this.Site = res.address;
                this.$nextTick(() => {
                    this.qrcode(res.address)
                })
            })
        }
    }

}
</script>

<style scoped lang='scss'>
.coin{
    font-size: 18px;
    .coin_cen{
        width: 100%;
        height: 100%;
        padding-top: 64px;
        .coin_title{
            >div{
                margin-top: 50px;
                display: flex;
                padding: 0 30px;
                span{
                    height: 40px;
                    line-height: 40px;
                    overflow:hidden;
                    text-overflow:ellipsis;
                    white-space:nowrap
                }
            }
            .code{
                width: 100%;
                height: 300px;
                border-top: 1px dashed #ddd;
                padding: 0;
                position: relative;

            }
            .btn{
                display: block;
                margin: 0;
                .btn1,.btn2{
                    margin-top: 10px;
                    border: none;
                    text-align: center;
                    border-radius: 10px;
                    color: white;
                    background:  #ff7f3b;
                }
            }
        }
    }
}
</style>
<style>
#qrcode{
    width: 150px;
    height: 150px;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
}
#qrcode>img{
    width: 100%;
    height: 100%;
}
</style>

